* {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  padding: 0 32px;
}
.App-main {
  height: 100vh;
  margin-top: 16px;
}

.App-header {
  display: flex;
  align-items: center;
  gap: 4px;
  border-bottom: 3px solid #E0354B;
  height: 35%;
}

.App-header img {
  width: 200px;
  height: auto;
}

.App-header h1 {
  font-size: 2rem;
  color: #E0354B;
}

.App-body {
  padding: 48px;
  height: 57%;  
}

.App-body form {  
  display: flex;
  gap: 12px;
  padding: 16px 0;
}

.App-body form .form-field {
 display: flex;
 gap: 12px;
 align-items: center;
 
}


.App-body form .form-field input {
  padding: 2px 8px;
  border-radius: 0px;
  border: 1px solid grey
}


.App-body form .form-submit button{
  padding: 2px 8px;
  border: 1px solid gray;
  border-radius: 4px;  
  background-color: white;
}

.App-body form .form-submit button:hover{
  background-color: gray;
  color: white;

  transition:ease-out 300ms;
}

.App-footer {
  border-top: 3px solid #E0354B;
  height: 8%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-footer p {
  font-style: italic;
}